.text {
  @include sections("light", "base");
  display: flex;
  gap: 20px;

  @media screen and (width<=800px) {
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin-block-end: clamp(1.5rem, -0.75rem + 6vw, 3.75rem);
  }

  &__content {
    flex-basis: 65%;

    p {
      opacity: 0.65;
      font-size: calc($font-size - 2px);
      line-height: 1.3;

      &:not(:last-of-type, .blockquote-expert__name) {
        margin-block-end: 20px;
      }
    }

    h2:not(.main-title, .text__title) {
      font-size: clamp(1.5rem, -0.125rem + 4.3333vw, 3.125rem);
      font-weight: 600;
      line-height: 1.1;
      margin-block: 40px 30px;
    }
    h3 {
      font-size: clamp(1.4rem, 1.5rem + 1.3333vw, 2.5rem);
      font-weight: 600;
      line-height: 1.1;
      margin-block: 40px 30px;
    }
    h4 {
      font-size: clamp(1.25rem, 0.75rem + 1.3333vw, 1.75rem);
      font-weight: 500;
      margin-block: 40px 30px;
    }

    ul {
      display: flex;
      flex-direction: column;
      gap: clamp(1rem, 0.5rem + 1.3333vw, 1.5rem);
      list-style: none;
      margin-block: 40px;

      li {
        padding-inline-start: 26px;
        position: relative;

        &::before {
          content: "";
          display: block;
          width: 10px;
          aspect-ratio: 1/1;
          background-color: $accent-color;
          border-radius: 50%;
          position: absolute;
          top: 7px;
          left: 0;
        }
      }
    }

    ol {
      display: flex;
      flex-direction: column;
      gap: clamp(1rem, 0.5rem + 1.3333vw, 1.5rem);
      list-style: none;
      margin-block: 40px;
      counter-reset: a;

      li {
        display: flex;
        gap: 15px;
        counter-increment: item;
        position: relative;

        &::before {
          content: "0" counter(item);
          color: $accent-color;
          font-weight: 500;
          line-height: 1.2;
        }

        @media (width<=700px) {
          gap: 10px;
        }
      }
    }

    blockquote {
      padding: 30px;
      border-radius: 30px;
      margin-block: 40px;
      background-color: $light-color;
      line-height: 1.36;
      font-style: italic;

      p {
        font-size: inherit;
        font-style: inherit;
        color: inherit;
        line-height: inherit;
      }

      &:has(.blockquote-expert) {
        display: flex;
        flex-direction: column;
        gap: 40px;

        @media (width<=1000px) {
          gap: 20px;
        }
      }
    }

    table {
      width: 100%;
      padding: 20px;
      background-color: #f3f9ff;
      border-radius: 24px;

      thead {
        padding: 25px 30px;
        background-color: $color-white;
        border-radius: 8px;

        th {
          font-size: 16px;
          opacity: 0.65;
          font-weight: 400;
          text-align: left;

          &:nth-child(1) {
            flex-basis: 35%;
          }
        }

        @media (width<=800px) {
          display: none;
        }
      }

      $font-size-20-16: clamp(1rem, 0.7917rem + 0.6667vw, 1.25rem);

      tr {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 14px 30px;
        font-size: $font-size-20-16;

        &:nth-child(even) {
          background-color: $color-white;
          border-radius: 8px;
        }

        @media (width<=800px) {
          .popup-btn {
            display: none;
          }
        }
      }

      button {
        font-size: $font-size-20-16;
        color: $accent-color;
        padding: 12px 16px 12px;
        box-shadow: 1px 1px 5px -1px rgb(255, 255, 255, 0);
        transition: box-shadow 300ms ease-out;

        &:hover {
          box-shadow: 1px 1px 5px -1px $accent-color;
        }
      }
    }

    @media screen and (width<=800px) {
      flex-basis: 100%;
    }
  }

  &__aside {
    flex-basis: 35%;
    max-width: 550px;
    display: flex;
    flex-direction: column;
    gap: 20px;

    &-image {
      width: 100%;
      object-fit: cover;
      object-position: center center;
      aspect-ratio: 1/1;
      border-radius: 30px;
      overflow: hidden;

      @media screen and (width<=800px) {
        display: none;
      }
    }

    .calculator {
      display: block;
      padding: 20px;
      border-radius: 24px;

      @media screen and (width<=800px) {
        display: none;
      }

      &::after {
        content: none;
      }

      &__title {
        font-size: 32px;
        margin-block-end: 16px;
      }

      &__block {
        padding: 20px;
        border-radius: 24px;
      }

      &__caption {
        margin-block-end: 40px;
      }

      &__button {
        padding: 8px;

        span {
          font-size: 16px;
        }

        .point-mark {
          width: 54px;
        }
      }
    }

    .preparation__plashka {
      border-radius: 24px;
      padding: 30px 38px 40px;

      @media screen and (width<=800px) {
        display: none;
      }

      p {
        font-size: 32px;
      }

      &::before,
      &::after {
        content: none;
      }

      .preparation__download {
        padding: 8px;

        span {
          font-size: 16px;
        }
        .point-mark {
          width: 54px;
        }
      }
    }
  }

  .blockquote-expert {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;

    img {
      display: block;
      overflow: hidden;
      border-radius: 50%;
      background-color: $color-white;
      object-fit: contain;
      object-position: center center;
      max-width: 80px;
      aspect-ratio: 1/1;
    }
    &__text {
      font-size: initial;
      font-style: initial;
    }

    &__name {
      font-size: $font-size;
      margin-block-end: 6px;
      opacity: 1;
    }

    &__job {
      opacity: 0.54;
      font-size: calc($font-size - 2px);
    }
  }
}

.reviewers {
  display: flex;
  flex-direction: column;
  gap: 20px;

  &__block {
    background-color: $light-color;
    padding: 25px 30px 30px;
    border-radius: 30px;

    @media screen and (width<=800px) {
      padding: 20px 14px 25px;
    }

    span {
      display: block;
      font-size: 16px;
      color: #133a66a6;
      margin-block-end: 40px;

      @media screen and (width<=800px) {
        margin-block-end: 20px;
      }
    }
  }

  &__expert {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 20px;

    &-img {
      display: block;
      overflow: hidden;
      border-radius: 50%;
      background-color: $color-white;
      object-fit: contain;
      object-position: center center;
      max-width: 80px;
      aspect-ratio: 1/1;
    }

    &-text {
      font-size: initial;
      font-style: initial;
    }

    &-name {
      font-size: $font-size;
      margin-block-end: 6px;
      opacity: 1;

      a {
        &:hover {
          color: $accent-color;
        }
      }
    }

    &-job {
      opacity: 0.54;
      font-size: calc($font-size - 2px);
    }
  }
}
